
<template>
  <div>
    <p>WebSocket Test</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <div>
      {{ws_status}}
    </div>
    <br>
    <div>
      <input type="text" v-model="ws_url" id="ws_url" placeholder="input ws url" style="width: 665px">
      <button type="button" @click="connect()">connect</button>
    </div>
    <br>
    <div>
      <input type="text" v-model="send_value" id="send_value" placeholder="send message" style="width: 580px">
      <button type="button" @click="send()">send</button>
      <button type="button" @click="clean_message()">clean message</button>
    </div>
    <br>
    <div>
      <textarea v-model="send_area" id="send_area" rows="10" cols="50" readonly placeholder="send area"/>
      <textarea v-model="receive_area" id="receive_area" rows="10" cols="50" readonly placeholder="receive area"/>
    </div>
  </div>
</template>

<script>

export default {
  name: 'WebSocketTestView',
  data () {
    return {
      ws_url: 'ws://localhost:8007/api/v1/sip/ws',
      ws_socket: null,
      ws_status: 'no connection',
      send_value: '',
      send_area: '',
      receive_area: ''
    }
  },
  methods: {
    connect () {
      if (this.ws_url !== '') {
        this.ws_socket = new WebSocket(this.ws_url)
        this.add_ws_event()
      } else {
        alert('ws url is empty')
      }
    },
    add_ws_event () {
      this.ws_socket.addEventListener('open', event => {
        this.ws_status = 'connection established'
      })
      this.ws_socket.addEventListener('close', event => {
        this.ws_status = 'no connection'
      })
      this.ws_socket.addEventListener('error', event => {
        console.log('ws error msg: ' + event)
      })
      this.ws_socket.addEventListener('message', event => {
        this.receive_area += event.data + '\n'
      })
    },
    send () {
      if (this.send_value === '') {
        return
      }
      if (this.ws_status === 'connection established') {
        this.ws_socket.send(this.send_value)
        this.send_area += this.send_value + '\n'
        this.send_value = ''
      } else {
        alert(this.ws_status)
      }
    },
    clean_message () {
      this.send_area = ''
      this.receive_area = ''
    }
  }
}
</script>
